@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
section#dashboard {
    display: flex;
    margin: 70px auto;
    max-width: 1140px;
    width: 100%;    
    flex-flow: row wrap;
}

section#dashboard article {
    width: 300px;
    height: 450px;
    background-color: #fff;
    border: 1px solid #00c853;
    border-radius: 7px;
    text-align: center;
    padding: 10px 0;
    margin-bottom: 50px;
}

article:nth-child(3n-1) {
    margin: 0 120px;
}

section#dashboard article img {
    border: 1px solid #00c853;
    border-radius: 7px;
    width: 280px;
    height: 280px;
    object-fit: cover;
}

section#dashboard .info {
    width: 100%;
    height: 100%;
    max-height: 145px;
    display: grid;
    align-content: space-between;
    padding: 0px 10px;
}

section#dashboard .info strong {
    height: 100%;
    font-size: 100%;
    max-height: 30px;
    color: #404040;
}

section#dashboard .info .price {
    font-size: 30px;
    color: #00c853;
    font-weight: bold;
}

section#dashboard .info .stock {
    color: #404040;
    font-weight: bold;
}

section#dashboard .btns {
    display: flex;
    justify-content: space-between;
}

.btn {
    width: 135px;
    height: 40px;
    cursor: pointer;
    border: none;
    color: #fff;
    font-weight: bold;
    font-size: 15px;
    border-radius: 7px;
}

.edit {
    background-color: #00c853;
}

.delete {
    background-color: #DC143C;
}
.details {
    display: flex;
    width: 100%;
    max-width: 1000px;
    margin: 50px auto;
    justify-content: space-between;
    border-radius: 7px;
    border: 1px solid #00c853;
    padding: 50px 50px 0 50px;
    background-color: #fff;
}

.details .left img {
    border: 1px solid #00c853;
    border-radius: 7px;
    max-width: 350px;
    width: 100%;
    height: 290px;
    object-fit: cover;
}

.details .left .btns-imgs {
    width: 100%;
    position: relative;
    z-index: 1;
    top: -50%;
    
    display: flex;
    justify-content: space-between;
}

.details .left .btns-imgs button {
    background-color: transparent;
    border: none;
    color: #00c853;
    cursor: pointer;
    font-size: 50px;
}

.details .right {
    width: 350px;
    height: 290px;
display: grid;
    justify-content: center;
    align-content: space-between;
    text-align: center;
}

.details .right strong {
    color: #404040;
    font-size: 30px;
}

.details .right .price {
    color: #00c853;
    font-weight: bold;
    font-size: 40px;
}

.details .right .stock {
    color: #404040;
    font-weight: 500;
    font-size: 25px;
}

.details .right .edit {
    margin-right: 20px;
}
form#addUpdate {
    display: flex;
    width: 100%;
    max-width: 1000px;
    margin: 50px auto;
    justify-content: space-between;
    border-radius: 7px;
    border: 1px solid #00c853;
    padding: 50px 50px;
    background-color: #fff;
}

form#addUpdate .left {
    max-width: 350px;
    width: 100%;
}

label#photos {
    display: flex;
    cursor: pointer;
    border: 3px dashed #ddd;
    border-radius: 7px;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

label#photos input {
    display: none;
}

label#photos.has-photos {
    border: 1px solid #00c853;
}

label#photos.has-photos img {
    display: none;
}

label#photos .switch-img {
    display: none;
}

label#photos .switch-div {
    width: 98%;
    display: flex;
    justify-content: space-between;
}

label#photos .switch-img-has {
    display: block;
    position: relative;
    justify-content: space-between;
    background-color: transparent;
    border: none;
    color: #00c853;
    cursor: pointer;
    font-size: 50px;
}

form#addUpdate .right {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
}

form#addUpdate .right .input-design {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
}

form#addUpdate .right label {
    color: #404040;
    text-align: center;
    display: flex;
    padding: 0 15px;
    align-items: center;
    border-radius: 7px 0 0 7px;
    border: 1px solid #00c853;
    height: 50px;
}

form#addUpdate .right input {
    height: 50px;
    width: 100%;
    padding: 15px;
    border: 1px solid #00c853;
    border-radius: 0 7px 7px 0;
    color: #404040;
    position: relative;
    right: 2px;
}

form#addUpdate .right button {
    height: 50px;
    width: 100%;
    color: #fff;
    background-color: #00c853;
    border: none;
    border-radius: 7px;
    font-size: 20px;
    cursor: pointer;
}
.header {
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 1px 8px #BDBDBD;
}

header {
    max-width: 1140px;
    width: 100%;
    margin: 0 auto;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
}

header img {
    height: 50px;
}

header .right-side {
    display: flex;
}

header .right-side .search {
    margin-right: 40px;
}

.search input {
    border: none;
    border-radius: 7px;
    background-color: #eee;
    height: 50px;
    width: 300px;
    padding: 0 10px;
    font-size: 20px;
    color: #404040;
}

.search button {
    position: relative;
    border: none;
    cursor: pointer;
    background-color: transparent;
    height: 0px;
    width: 0px;
    left: -30px;
}

.add button{
    height: 50px;
    width: 200px;
    border-radius: 7px;
    border: none;
    background-color: #00c853;
    font-size: 20px;
    color: #fff;
    cursor: pointer;
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
}

body {
    background-color: #f5fffa;
}
/*320, 480, 760, 960, 1200, 1600*/

@media (max-width: 1200px) {
    .header {
        padding: 0 20px;
    }
    section#dashboard {
        padding: 20px;
    }

    section#dashboard article {
        margin: 0 auto;
        margin-bottom: 50px;
    }

    article:nth-child(3n-1) {
        margin: 0;
    }
}

@media (max-width: 1020px) {
    form#addUpdate {
        width: 100%;
        max-width: 900px;
        margin: 50px auto ;
    }
}

@media (max-width: 920px) {
    form#addUpdate, .details {
        max-width: 500px;
        flex-direction: column;
        align-items: center;
    }
    form#addUpdate .left, .details .left {
        max-width: 350px;
        width: 100%;
        height: 350px;
        margin-bottom: 20px;
    }
    form#addUpdate .right, .details .right {
        max-width: 350px;
        width: 100%;
    }
}

@media (max-width: 702px) {
    .search input {
        width: 200px;
        height: 40px;
        font-size: 14px;
    }
    header img {
        margin-right: 20px;
    }
    .add {
        display: flex;
        align-items: center;
    }
    .add button {
        width: 100px;
        font-size: 14px;
        height: 40px;
    }
}

@media (max-width: 520px) {
    form#addUpdate, .details {
        max-width: 360px;
        padding: 5px 5px;
    }
}

@media (max-width: 480px) {
    .search input {
        width: 150px;
        font-size: 10px;
        height: 40px;
    }
    .add button {
        width: 80px;
        font-size: 14px;
        height: 40px;
    }
    header img {
        height: 30px;
        margin-right: 5px;
    }
    header .right-side .search {
        margin-right: 15px;
    }
}

